import * as React from 'react';
import Chevron from 'nav-frontend-chevron';
import * as moment from 'moment';
import { DatovelgerAvgrensninger } from './types';
import { erDatoEtterMinDato, erDatoFørSluttdato } from './utils/datovalidering';

// interface NavbarKnapp {
// 	label: string;
// 	onClick: (event: React.MouseEvent<HTMLButtonElement>) => void;
// 	disabled?: boolean;
// }

interface Props {
	måned: Date;
	byttMåned: (month: Date) => void;
	avgrensninger?: DatovelgerAvgrensninger;
}

const Navbar: React.StatelessComponent<Props> = ({
	måned,
	byttMåned,
	avgrensninger
}) => {
	const forrige = moment(måned)
		.add(-1, 'months')
		.endOf('month');

	const neste = moment(måned)
		.add(1, 'months')
		.startOf('month');

	const onClick = (evt: React.MouseEvent<HTMLButtonElement>, mnd: Date) => {
		evt.preventDefault();
		evt.stopPropagation();
		byttMåned(mnd);
	};

	return (
		<div className="nav-dagvelger__navbar">
			<button
				className="nav-dagvelger__navbar__knapp nav-dagvelger__navbar__knapp--forrige"
				type="button"
				onClick={(e) => onClick(e, forrige.toDate())}
				disabled={
					avgrensninger
						? !erDatoEtterMinDato(forrige.toDate(), avgrensninger.minDato)
						: false
				}
				aria-label={`Gå til ${forrige.format('DDDD YYYY')}`}
			>
				<Chevron type="venstre" />
			</button>
			<button
				type="button"
				onClick={(e) => onClick(e, neste.toDate())}
				disabled={
					avgrensninger
						? !erDatoFørSluttdato(neste.toDate(), avgrensninger.minDato)
						: false
				}
				aria-label={`Gå til ${neste.format('DDDD YYYY')}`}
				className="nav-dagvelger__navbar__knapp nav-dagvelger__navbar__knapp--neste"
			>
				<Chevron type="høyre" />
			</button>
		</div>
	);
};

export default Navbar;



